import React, { useContext, useState } from "react";
import { theme } from "../../theme";
import TextStyled from "../TextStyled";
import { LoginButtonAzureAD } from "./styles";
import azureADLogo from "../../assets/icons/logo-ad-connect.png";
import { useMsal } from "@azure/msal-react";
import { AuthContext } from "../../contexts/auth";

export default function ButtonAzureAD() {
  const { instance } = useMsal();
  const { setIsSigned } = useContext(AuthContext);

  return (
    <LoginButtonAzureAD
      onClick={() => {
        instance
          .loginRedirect()
          .then(() => {
            setIsSigned(true);
            localStorage.setItem("@raspToken");
          })
          .catch((e) => {
            console.log(e);
            setIsSigned(false);
          });
      }}
    >
      <img style={{ height: 40, width: 40 }} src={azureADLogo} alt="" />
      <TextStyled large center color={theme.primary}>
        Login con Azure AD
      </TextStyled>
      <div style={{ height: 40, width: 40 }} />
    </LoginButtonAzureAD>
  );
}
